<template>
  <div class="container gap-5 mx-auto lg:grid lg:grid-cols-2">
    <div
      class="flex flex-col justify-center"
      style="min-height: calc(100vh - 200px) !important"
    >
      <div class="px-5 py-10 bg-white rounded shadow-xl">
        <div
          :class="login_alert_variant"
          class="p-5 mb-4 font-bold text-center text-white"
          v-if="login_show_alert"
        >
          {{ login_alert_msg }}
        </div>
        <vee-form :validation-schema="schema" @submit="login">
          <h2 class="text-2xl font-semibold text-center dark:text-white">
            Login with Credentials
          </h2>
          <!-- Email -->
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white">Email</label>
            <vee-field
              type="email"
              name="email"
              class="block w-full px-3 py-3 text-xl font-medium bg-gray-200 border-none outline-none "
              placeholder="Enter Email"
            />
            <ErrorMessage class="text-red-600" name="email" />
          </div>

          <!-- Password -->
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white">Password</label>
            <vee-field
              name="password"
              :bails="false"
              v-slot="{ field, errors }"
            >
              <input
                type="password"
                class="block w-full px-3 py-3 text-xl font-medium bg-gray-200 border-none outline-none "
                placeholder="Password"
                v-bind="field"
              />
              <div class="text-red-600" v-for="(error, i) in errors" :key="i">
                {{ error }}
              </div>
            </vee-field>
          </div>

          <button
            type="submit"
            class="block w-full px-3 py-3 text-xl text-white transition bg-red-500 rounded  hover:bg-red-600"
            :disabled="login_in_submission"
          >
            Login
          </button>
        </vee-form>
        <div>
          <router-link
            to="/register"
            class="block w-full px-3 py-3 mt-3 text-xl text-center text-white transition bg-blue-500 rounded  hover:bg-blue-600"
          >
            Register
          </router-link>
        </div>
        <div>
          <a
            href="https://youtube.com/playlist?list=PLqyVsQxV6gqfpfLl_golBk44xx3TopB09"
            target="_blank"
            class="block w-full px-3 py-3 mt-3 text-xl text-center text-white transition bg-green-500 rounded  hover:bg-green-600"
            >Tutorial</a
          >
        </div>
      </div>
    </div>
    <div class="items-center justify-center hidden mt-20 lg:flex">
      <img src="../../assets/banner.png" alt="" class="" />
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginFrom",

  data() {
    return {
      schema: {
        email: "required|min:3|max:100|email",
        password: "required|min:3|max:100",
      },

      login_in_submission: false,
      login_show_alert: false,
      login_alert_variant: "bg-blue-500",
      login_alert_msg: "Please wait! Untill your login in progress.",
    };
  },
  methods: {
    async login(values) {
      this.login_show_alert = true;
      this.login_in_submission = true;
      this.login_alert_variant = "bg-blue-500";
      this.login_alert_msg =
        "Please wait! Untill your login has been completed";

      try {
        await this.$store.dispatch("login", {
          values: values,
          router: this.$router,
          route: this.$route,
        });
      } catch (err) {
        this.login_in_submission = false;
        this.login_alert_variant = "bg-red-500";
        this.login_alert_msg = err.message;
        return;
      }

      this.login_alert_variant = "bg-green-500";
      this.login_alert_msg = "Success! Your account has been logged in.";
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
